<template>
  <div class="login">
    <!-- Inicio de sesi√≥n -->
    <div v-if="currentView === 'login'">
      <h2>Iniciar Sesi√≥n</h2>
      <form @submit.prevent="authenticate">
        <input v-model="email" type="email" placeholder="Email" required />
        <input v-model="password" type="password" placeholder="Contrase√±a" required />
        <button type="submit">Entrar</button>
        <p><a @click.prevent="switchToRegister" href="#">Reg√≠strate aqu√≠</a></p>
      </form>
    </div>

    <!-- Vista para mostrar informaci√≥n del usuario autenticado -->
    <div v-else-if="currentView === 'dashboard'">
      <h2>Bienvenido, {{ user?.email }}</h2>
      <button @click="logout">Cerrar sesi√≥n</button>
    </div>

    <!-- Cambio de vista a registro (RegisterForm) -->
    <div v-else-if="currentView === 'register'">
      <RegisterForm @switchToLogin="switchToLogin" />
    </div>

    <!-- Contemplacion de errores -->
    <div v-else>
      <p>Error inesperado. Intenta recargar la p√°gina.</p>
    </div>
  </div>
</template>

<script setup>
import { ref, defineEmits } from "vue"; // importaci√≥n de ref y emits para reactividad
import { useCurrentUser } from "vuefire"; // usuario actual
import { getAuth, signInWithEmailAndPassword, signOut } from "firebase/auth"; // m√©todos propios de Firebase
import RegisterForm from './RegisterForm.vue'; // uni√≥n con el componente de registro

// Emite eventos al componente padre
const emit = defineEmits(["authenticated", "loggedOut"]); // Se agrega "loggedOut" para manejar el cierre de sesi√≥n

/* Declaraci√≥n de variables reactivas */
const email = ref(""); // email
const password = ref(""); // contrase√±a
const user = useCurrentUser(); // estado del usuario actual
const currentView = ref("login"); // control de la vista actual ('login', 'dashboard')

/* Autenticaci√≥n del usuario en Firebase */
async function authenticate() {
  const auth = getAuth(); // inicializa la autenticaci√≥n
  try {
    await signInWithEmailAndPassword(auth, email.value, password.value); // verifica el usuario con Firebase
    alert("Inicio de sesi√≥n exitoso");
    emit("authenticated"); // emite el evento autenticado
    currentView.value = "dashboard"; // Cambia a la vista del dashboard
  } catch (error) {
    console.error("Error al iniciar sesi√≥n:", error);
    if (error.code === "auth/user-not-found") {
      alert("El usuario no existe. Por favor, verifica el email.");
    } else if (error.code === "auth/wrong-password") {
      alert("Contrase√±a incorrecta. Por favor, int√©ntalo de nuevo.");
    } else if (error.code === "auth/invalid-email") {
      alert("Email inv√°lido. Por favor, introduce un email v√°lido.");
    } else {
      alert("Ocurri√≥ un error inesperado al iniciar sesi√≥n.");
    }
  }
}

/* Cierre de sesi√≥n del usuario actual */
async function logout() {
  const auth = getAuth(); // inicializa la autenticaci√≥n
  try {
    await signOut(auth); // cierra la sesi√≥n
    alert("Has cerrado sesi√≥n");
    emit("loggedOut"); // Emite el evento para que el padre maneje el cierre de sesi√≥n
    currentView.value = "login"; // Cambia la vista al login
  } catch (error) {
    console.error("Error al cerrar sesi√≥n:", error);
    alert("Ocurri√≥ un error inesperado al cerrar sesi√≥n.");
  }
}

/* Cambio a la vista de registro */
function switchToRegister() {
  currentView.value = "register"; // Cambia a la vista de registro
}

/* Cambiar a la vista de inicio de sesion (Login) */
function switchToLogin() {
  currentView.value = "login";
}
</script>

<style scoped>
/* Contenedor principal */
.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f4f4f4;
  color: white;
}

/* Formulario */
form {
  display: flex;
  width: 500px;
  flex-direction: column;
  gap: 10px;
}

/* Boton */
button {
  padding: 10px 20px;
  background-color: rgb(156, 0, 0);
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: rgb(120, 0, 0);
}
</style>
