import { createApp } from 'vue';
import { VueFire, VueFireAuth } from 'vuefire'; // Importado de VueFire y autenticaci√≥n
import { firebaseApp } from './firebaseConfig'; // Importado de la configuraci√≥n de firebase
import App from './App.vue';

const app = createApp(App);

app.use(VueFire, {
  firebaseApp, // Conexi√≥n con firebase
  modules: [
    VueFireAuth(),
  ],
});

app.mount('#app');

